li {
  list-style: none;
}

a {
  text-decoration: none;
}

.sidebar {
  color: white;
  position: fixed;
  padding: 150px 10px 0 10px;
  top: 0;
  left: 0;
  z-index: 100;
  visibility: hidden;
  width: 100vw;
  height: 100%;
  background: #80CBC4;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

@media (min-width: 48em) {
  .sidebar {
    padding: 205px 10px 0 10px;
    width: 50vw;
  }
}

@media (min-width: 64em) {
  .sidebar {
    padding: 156px 10px 0 10px;
    width: 30vw;
  }
}

.sidebar:after {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  content: '';
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.sidebarPusher {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99;
  height: 100%;
  perspective: 1000px;
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
}

.sidebarPusher::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  background: rgba(0, 0, 0, 0.2);
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s;
  transition: opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s;
}

.sidebarOpen .sidebarPusher {
  width: 100%;
}

.sidebarOpen .sidebarPusher::after {
  width: 100%;
  height: 100%;
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.sidebarOpen .sidebar:after {
  width: 0;
  height: 0;
  opacity: 0;
  -webkit-transition: opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s;
  transition: opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s;
}

.sidebar {
  visibility: visible;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}

.sidebarOpen .sidebar {
  box-shadow: 7px 0px 3px -5px #737373;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.sidebar::after {
  display: none;
}

.counterButton {
  background: #80DEEA;
  font-size: 2rem;
  position: fixed;
  bottom: 20vh;
  left: 110vw;
}

@media (min-width: 48em) {
  .counterButton {
    left: 55vw;
  }
}

@media (min-width: 64em) {
  .counterButton {
    left: 32vw;
  }
}

@media (max-width: 767px) {
  .sidebarOpen .counterButton {
    left: initial;
    right: 0;
  }
}

.resultList {
  font-family: Georgia, Times, serif;
  font-size: 15px;
}

.resultList ul li:first-of-type a {
  border-radius: 4px 4px 0 0;
}

.resultList ul li:last-of-type a {
  border-radius: 0 0 4px 4px;
}

.resultList ul li a {
  display: block;
  color: #000000;
  background-color: #FFFFFF;
  line-height: 30px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #CCCCCC;
  padding-left: 10px;
  cursor: pointer;
}

.resultList ul li a:hover {
  color: #FFFFFF;
  background-color: #607D8B;
  background-repeat: repeat-x;
}

.resultList ul li a strong {
  margin-right: 10px;
}

.selectState {
  display: inline;
  position: relative;
}

.selectState a {
  display: inline-block;
  text-decoration: underline;
  color: #546E7A;
  padding: 5px 0;
  transition: all 0.5s ease-in-out;
  min-width: 10px;
}

.selectState:hover a {
  min-width: 100px;
  text-align: center;
  padding: 5px;
  text-decoration: none;
  background-color: #1DE9B6;
}

.selectState ul {
  display: none;
  padding: 0;
  position: relative;
  color: #546E7A;
  top: 3px;
  left: 89px;
  list-style: none;
}

.selectState ul li[data-selected='true'] {
  display: none;
}

.selectState ul li {
  width: 100px;
  transition: all 0.3s ease-in-out;
}

.selectState ul li:hover {
  text-align: center;
  padding: 5px;
  background-color: #1DE9B6;
}

.selectState:hover ul {
  cursor: pointer;
  display: block;
}

.content {
  box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.7);
}

.content ul {
  padding: 0;
}

.content .menu {
  position: relative;
}

.content .menu:nth-last-child(n+2) .menuTitle {
  border-bottom: 3px solid #db3f21;
}

.content .menu .menuTitle {
  position: relative;
  color: white;
  background-color: #E4644B;
  padding: 1.5rem 1rem;
  margin: 0 auto;
  cursor: pointer;
}

.content .menu .menuTitle:hover {
  background-color: #db3f21;
}

.content .menu .menuTitle h3 {
  margin: 0;
}

.content .menu .menuItemList {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
  background-color: #CDDC39;
}

.content .menu .menuItemList .metaDisplay {
  padding: 0 10px;
}

.content .menu .menuItemList h4 {
  margin-left: 10px;
}

.content .menu.menuOpen .menuItemList {
  padding: 10px 0;
  max-height: 1000px;
}

.content .menu.menuOpen .menuItemListPadding {
  padding: 10px 15px;
  max-height: 1000px;
}

.content .menu.menuOpen .menuTitle {
  border-bottom: 0;
  box-shadow: 0px -3px 3px 0px rgba(124, 36, 21, 0.7), 0px 3px 3px 0px rgba(124, 36, 21, 0.7);
  position: relative;
  width: 101%;
  margin-left: -0.5%;
}

.content .savedPageMenu .loginRequired {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(180, 180, 180, 0.5);
  text-align: right;
  padding: 2rem;
  margin: 0;
}

.content .savedPageMenu .savePageButton {
  position: absolute;
  height: 80%;
  top: 10%;
  right: 1%;
}

.content .savedPageMenu .savePageButton * {
  margin: 0;
}

.movieListItem {
  padding: 5px 5px 5px 25px;
}

.movieListItem strong {
  cursor: pointer;
}

.movieListItem > span:last-of-type, .movieListItem > span:nth-last-of-type(2) {
  cursor: pointer;
  float: right;
  background-color: #1DE9B6;
  color: #FF7043;
  padding: 0 10px;
  transition: all 2s ease-in-out;
  max-width: 29px;
}

.movieListItem > span:last-of-type:after, .movieListItem > span:nth-last-of-type(2):after {
  display: none;
  content: 'earch';
}

.movieListItem > span:last-of-type:hover, .movieListItem > span:nth-last-of-type(2):hover {
  max-width: 999px;
}

.movieListItem > span:last-of-type:hover:after, .movieListItem > span:nth-last-of-type(2):hover:after {
  display: inline;
}

.movieListItem > span:nth-last-of-type(2) {
  float: right;
  background-color: #FFEA00;
  color: #ff00d0;
}

.movieListItem > span:nth-last-of-type(2):after {
  display: none;
  content: 'pdate';
}

.movieListItem:nth-of-type(2n) {
  background-color: #484141;
}

.movieListItem:nth-of-type(2n+1) {
  background-color: #635959;
}

.hidden {
  display: none;
}



