.popover {
  position: fixed;
}

.content {
  margin: 5vh 0;
  column-count: 1;
  column-gap: 0;
  column-rule: 3px dashed #bfbfbf;
}

@media (min-width: 48em) {
  .content {
    margin: 5vh -5vw;
    column-count: 2;
    column-gap: 2;
  }
}

@media (min-width: 64em) {
  .content {
    margin: 5vh -10vw;
    column-count: 4;
    column-gap: 3;
  }
}

.playModal {
  width: 90vw;
}

.qualitySelector {
  float: right;
  margin-right: 17.5%;
}

.qualitySelector div {
  display: inline;
}

.qualitySelector input[type=checkbox] {
  display: none;
}

.qualitySelector input[type=checkbox] + span {
  color: #bfbfbf;
}

.qualitySelector input[type=checkbox]:checked + span {
  color: red;
}



