@import url("https://fonts.googleapis.com/css?family=Great+Vibes|Damion|Roboto:400,500");
body {
  font-family: "Roboto", Helvetica, sans-serif;
}

.app :global(.navbar) {
  box-shadow: 0px 10px 3px -8px #737373;
}

.app :global(.navbar-brand) {
  font-family: 'Great Vibes', cursive;
  font-size: 3rem;
  font-weight: bold;
  top: 5px;
  position: relative;
  padding-left: 50px;
  width: 100%;
}

@media (min-width: 48em) {
  .app :global(.navbar-brand) {
    width: initial;
  }
}

.app :global(.navbar-nav) {
  font-weight: 500;
}

.app .brand {
  position: absolute;
  top: 0;
  left: 5px;
  display: inline-block;
  background: url("../Home/logo.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 80%;
  margin: 0 10px 0 0;
  border-radius: 20px;
}

.app nav :global(.fa) {
  font-size: 2em;
  line-height: 20px;
}

@media (max-width: 767px) {
  .app :global(.navbar-fixed-top) {
    position: relative;
    top: auto;
  }
  .app :global(.navbar-fixed-top) :global(.navbar-brand) {
    position: fixed;
    z-index: 100;
    background: #f8f8f8;
  }
  .app :global(.navbar-nav) {
    margin-top: 40px;
  }
  .app body {
    margin: 0;
  }
}

.appContent {
  margin: 0;
  padding: 24px 0;
  background-color: whitesmoke;
}

@media (min-width: 48em) {
  .appContent {
    margin: 105px 0;
  }
}

@media (min-width: 64em) {
  .appContent {
    margin: 56px 0;
  }
}



