.movie-content {
  position: relative;
  z-index: 0;
  margin: 0 2vw;
}

@media (min-width: 48em) {
  .movie-content {
    margin: 0 5vw;
  }
}

@media (min-width: 64em) {
  .movie-content {
    margin: 0 7vw;
  }
}

@keyframes extendedViewTransform {
  0% {
    width: 120%;
  }
  100% {
    width: 80%;
  }
}

.categoryName {
  font-family: 'Damion', cursive;
  color: #FF4081;
  cursor: pointer;
  display: inline-block;
  position: relative;
  left: 0;
  transition: all 0.5s ease-in-out;
}

.categoryName h2 {
  transition: all 0.5s ease-in-out;
  font-weight: 500;
  position: relative;
  margin: 0;
}

.categoryName.extended {
  left: 50%;
  color: #FF5722;
  transition: all 0.5s ease-in-out;
  transform: translateX(-50%);
}

.categoryName.extended + div {
  overflow: hidden;
  animation: extendedViewTransform 2s forwards;
}

.categoryName.extended h2 {
  font-size: 5rem;
  margin-bottom: 4rem;
}



